import React from "react";

const Modal_B = props => (
	<div  id="services">
      

       <div className="container">
       <h2>Xin cho  {props.city &&  <td> Thnh ph: { props.city } </td> }</h2>
       {props.id &&  <td> M s a danh: { props.id } </td> }            
       <table className="table table-drak">
         <thead>
           <tr>
              <th></th>
             <th>1</th>
             <th>2</th>
             <th>3</th>
             <th>4</th>
           </tr>
         </thead>
         <tbody>
           <tr>
             <td>V tr</td>
             {props.coord &&  <td> Tung : { props.coord } </td> }
             {props.coord2 &&  <td> V : { props.coord2 } </td> }
           </tr>
           <tr>
             <td>Thi tit</td>
             {props.description &&  <td> Thi tit: { props.description } </td> }
             {props.icon &&  <td> Biu tng: { props.icon } <img src="http://openweathermap.org/img/w/10d.png"></img> </td> }
             {props.id2 &&  <td> M s th tit: { props.id2 } </td> }
             {props.main &&  <td> thi tit hin ti : { props.main } </td> }
           </tr>
           <tr>
             <td>i kh tng</td>
             {props.base &&  <td> o c ti: { props.base } </td> }
           </tr>
           <tr>
           <td>Thng tin chnh</td>
           {props.humidity &&  <td>  m { props.humidity } (%) </td> }
           {props.temp_max &&  <td> Nhit  cao nht { props.temp_max }  ( C)</td> }
           {props.temp_min &&  <td> Nhit  thp nht { props.temp_min }  ( C) </td> }
           {props.temperature &&  <td> Nhit  hin ti{ props.temperature }  ( C) </td> }
           
         </tr>


         <tr>
         <td>Thng tin gi</td>
         {props.wind &&  <td> Tc  gi{ props.wind} (m/s) </td> }
         {props.clould &&  <td>  che ph ca my{ props.clould} (%) </td> }
       </tr>


       <tr>
       <td>H thng</td>
       {props.country &&  <td> Quc gia{ props.country} </td> }
       {props.message &&  <td> Thi gian phn hi{ props.message} (giy) </td> }
       {props.sunrise &&  <td> V tr mt tri mc{ props.sunrise} </td> }
       {props.sunset &&  <td> V tr mt tri ln{ props.sunset} </td> }
     </tr>



         </tbody>
       </table>
       </div>
    


           
         
      
      
    </div>
);

export default Modal_B;




